import { UChild } from './UChild';
import { UContainer } from './UContainer';
import { Terminal, UniCircuit, Trigger, ChangeSet } from '../Circuits'
import { Circuit } from '../Circuits';
import { USomeService } from './USomeService';
import { ULocalService } from './ULocalService';
import { ChangeEvent } from 'react';

// @x.Register()
export class UGrandContainer extends UniCircuit
{
    onRadioMessageChanged(e: ChangeEvent<HTMLTextAreaElement>)
    {
        this.t_RadioMessage = e.target.value;
    }
    
    //#region NGivenName
    //#######################################
    @Terminal()
    t_GivenName = 'Grand';
    //------------------------
    //#######################################
    //#endregion

    @Terminal()
    t_RadioMessage = '';

    //#region NName
    //#######################################
    @Terminal()
    t_Name = 'Grand';
    //----------------------------
    @Trigger(()=>UGrandContainer,    x=>x.t_GivenName)
    @Trigger(()=>UChild,             x=>x.t_Name)
    @Trigger(()=>UContainer,         x=>x.t_Name)
    NName_(ch: ChangeSet): string
    {
        return `${this.t_GivenName}/${this.u_Container.t_Name}/${this.u_Container.uChild.t_Name} --- `;
    }
    //#######################################
    //#endregion


    @Circuit(()=>UContainer)
    u_Container!: UContainer;

    @Circuit(()=>USomeService)
    u_SomeService!: USomeService;

    @Circuit(()=>ULocalService)
    u_RadioClient!: ULocalService;


    private onRadioMessage(msg: string)
    {
        console.warn(`newwwwwwwwwwwwwwwwwwwwwww radio message ${msg}`)
    }

}