import { Circuit, Terminal, UniCircuit } from "../Circuits";
import { USomeService } from "./USomeService";

export enum View
{
    about= 'about',
    home= 'home',
}

export class URoot extends UniCircuit
{
    @Terminal()
    t_ActiveView = View.about;

    @Circuit(()=>USomeService)
    u_SomeService!: USomeService;
    
    navToAbout()
    {
        this.t_ActiveView = View.about;
    }

    name = 'Georgex'

    isAbout()
    {
        return this.t_ActiveView === View.about;
    }

    isHome()
    {
        return this.t_ActiveView === View.home;
    }

    navToHome()
    {
        this.t_ActiveView = View.home;
    }
}