import logo from './logo.svg';

import './App.css';
import {  CircuitView } from './Circuits/react'
import { UGrandContainer } from './LogicalApp/UGrandContainer';
import { contracts_CircuitBench } from 'wv-circuit-bench-contracts';
import { Motherboard } from './Circuits';

class App extends CircuitView<{circuit: UGrandContainer}>
{
  test = "g";

  override circuitViewWillUnmount(): void
  {
    
  }

  async send(e: React.MouseEvent<HTMLButtonElement, MouseEvent>)
  {
    const envelop = new contracts_CircuitBench.Vlp_Ping(Motherboard.workspace!, 'PING');
    await envelop.postAsync();
    alert(JSON.stringify(envelop.response));
  }

  render() 
  {
    (window as any).c = this;
    // console.warn(this.Circuit?.NCalculated);
    return (
      <div className="App">
        
        <h1> :::: {this.circuit.t_Name}</h1>
        {/* <h1>this.Circuit!.NCalculated = {this.Circuit!.NCalculated}  ***</h1> */}

        <textarea value={this.circuit.t_RadioMessage} 
                  onChange={(e)=>this.circuit.onRadioMessageChanged(e)}>

        </textarea>
        
        <button onClick={(e)=>this.send(e)}>SEND</button>
        <img src={'https://www.google.com/logos/doodles/2022/2022-world-cup-quarter-finals-dec-9-10-6753651837110006.2-ladc.gif'} className="App-logo" alt="logo" />
        
      </div>
    );
  }
}
export default App;
